<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <el-input v-model="queryInfo.query" placeholder="" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="userlist" stripe border style="width: 100%">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="" prop="mg_state">
          <!--
            el-table-column ,  prop , 
             element, UI,    ui 
            , ,  
          -->
          <template slot-scope="scope">
            <!-- {{ scope.row }} -->
            <!-- scope.row  row  element , :  -->
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delUserById(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 8, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRoleDialogVisible"
      width="50%"
      @close="setRoleDialogClose"
    >
      <div class="set-role">
        <p>: {{userInfo.username}}</p>
        <p>: {{userInfo.role_name}}</p>
        <p>
          :
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Users',
  data() {
    // 
    const checkMobile = (rule, value, callback) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) return callback()
      callback(new Error(''))
    }
    return {
      // 
      queryInfo: {
        query: '', // 
        pagenum: 1, // 
        pagesize: 5 // 
      },
      userlist: [], // 
      total: 0, // 
      addDialogVisible: false, // 
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 10, message: ' 5  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: '', trigger: 'blur' }
        ],
        // 
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            type: 'email',
            message: '',
            trigger: ['blur']
          }
        ],
        // 
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },

      // 
      editDialogVisible: false,

      // 
      editForm: {},
      // 
      editFormRules: {
        // 
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            type: 'email',
            message: '',
            trigger: ['blur']
          }
        ],
        // 
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },

      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // id
      selectedRoleId: ''
    }
  },

  created() {
    // 
    this.getUserList()
  },

  methods: {
    // 
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)

      this.userlist = res.data.users
      this.total = res.data.total
    },

    //  pageSize 
    handleSizeChange(newSize) {
      // console.log(` ${newSize} `)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //   
    handleCurrentChange(newNum) {
      // console.log(`: ${newNum}`)
      this.queryInfo.pagenum = newNum
      this.getUserList()
    },

    //  switch
    async userStateChanged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.message.error(res.meta.msg)
      }
      return this.$message.success(res.meta.msg)
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    // 
    addUser() {
      // 
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return

        // 
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // 
        this.addDialogVisible = false
        this.getUserList()
      })
    },

    //  , id
    async showEditDialog(id) {
      // 
      this.editDialogVisible = true

      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editForm = res.data
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // ,    
    editUser() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.editDialogVisible = false
        this.getUserList()
      })
    },

    // id
    async delUserById(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      // confirmResult confirm, 
      // confirmResult cancel, 
      if (confirmResult !== 'confirm') return this.$message.info('')

      // 
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 
      this.getUserList()
    },

    // 
    async setRole(userInfo) {
      this.userInfo = userInfo

      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.rolesList = res.data

      this.setRoleDialogVisible = true
    },

    //  
    setRoleDialogClose() {
      this.selectedRoleId = ''
      this.userInfo = {}
    },

    // , 
    async saveRoleInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error('')
      }

      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectedRoleId
        }
      )
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      this.getUserList()
      // 
      this.setRoleDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.set-role p {
  margin-bottom: 10px;
}
</style>
