<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        :data="catelist"
        :columns="columns"
        :show-row-hover="false"
        :selection-type="false"
        :expand-type="false"
        index-text="#"
        show-index
        border
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            style="color: lightgreen"
            v-if="scope.row.cat_deleted === false"
          ></i>
          <i class="el-icon-error" style="color: red" v-else></i>
          <!-- <pre>{{scope.row}}</pre> -->
        </template>

        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" size="mini" v-else></el-tag>
          <!-- <pre>{{scope.row}}</pre> -->
        </template>

        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditCateDialog(scope.row.cat_id)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeCateById(scope.row.cat_id)"
          ></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      @close="addCateDialogClosed"
      width="50%"
    >
      <!--  -->
      <el-form
        ref="addCateFormRef"
        :model="addCateForm"
        :rules="addCateFormRules"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>

        <el-form-item label=":">
          <!-- options - 
               props  -  
          -->
          <el-cascader
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            clearable
            @change="prentCateChanged"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editCateDialogVisible"
      @close="editCateDialogClosed"
      width="50%"
    >
      <!--  -->
      <el-form
        ref="editCateFormRef"
        :model="editCateForm"
        :rules="editCateFormRules"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Category',
  data() {
    return {
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      catelist: [],
      // 
      total: 0,
      //  Array
      columns: [
        {
          label: '', // 
          prop: 'cat_name'
        },
        {
          label: '',
          type: 'template',
          template: 'isok'
        },
        {
          label: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 
      addCateDialogVisible: false,
      // 
      addCateForm: {
        cat_name: '', // 
        cat_pid: 0, //  ID
        cat_level: 0 // 
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      parentCateList: [],
      //  id 
      selectedKeys: [],
      // 
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true // 
      },
      // 
      editCateDialogVisible: false,
      // 
      editCateForm: {},
      // 
      editCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)

      this.catelist = res.data.result
      this.total = res.data.total
    },

    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getCateList()
    },

    // 
    async showAddCateDialog() {
      // 
      this.getPrentCatelist()

      // 
      this.addCateDialogVisible = true
    },

    // 
    async getPrentCatelist() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.parentCateList = res.data
    },

    // id
    prentCateChanged() {
      // selectedKeys  length 0 
      // 0  
      if (this.selectedKeys.length > 0) {
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ]
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },

    // , 
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },

    // 
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return

        const { data: res } = await this.$http.post(
          'categories',
          this.addCateForm
        )
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)

        this.getCateList()
        this.addCateDialogVisible = false
      })
    },

    // 
    async showEditCateDialog(id) {
      // 
      this.editCateDialogVisible = true

      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editCateForm = res.data
    },

    // , 
    editCateDialogClosed() {
      this.$refs.editCateFormRef.resetFields()
    },

    // 
    editCate() {
      this.$refs.editCateFormRef.validate(async valid => {
        if (!valid) return

        const { data: res } = await this.$http.put(
          `categories/${this.editCateForm.cat_id}`,
          {
            cat_name: this.editCateForm.cat_name
          }
        )

        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.getCateList()
        this.editCateDialogVisible = false
      })
    },

    // id
    async removeCateById(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped>
.zk-table {
  margin: 15px 0 !important;
  font-size: 14px;
}

/deep/ .zk-table--firstProp-header-inner {
  padding-left: 12px !important;
}

/deep/ .zk-table__header-row {
  color: #909399 !important;
}

/deep/ .zk-table__body-row {
  color: #606266;
}

.el-cascader {
  width: 100%;
}
</style>
