<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>

      <!--  -->
      <el-steps :active="+activeIndex" align-center finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tab -->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRules" size="medium">
        <el-tabs
          tab-position="left"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <!--  -->
          <el-tab-pane label="" name="0">
            <!--  -->
            <el-form-item label=":" prop="goods_name" label-width="100px">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label=":" prop="goods_price" label-width="100px">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label=":" prop="goods_weight" label-width="100px">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label=":" prop="goods_number" label-width="100px">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label=":" prop="goods_cat" label-width="100px">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :key="item.attr_id"
              v-for="item in manyTabData"
              :label="item.attr_name + ':' "
              label-width="60px"
            >
              <el-checkbox-group v-model="item.attr_vals" size="mini">
                <el-checkbox
                  :key="index"
                  v-for="(item2, index) in item.attr_vals"
                  :label="item2"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :key="item.attr_id"
              v-for="item in onlyTabData"
              :label="item.attr_name + ':' "
              label-width="160px"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- action API -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="addBtn" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt width="100%" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  name: 'AddGoods',
  data() {
    return {
      // 
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '', // 
        goods_price: 0, // 
        goods_weight: 0, // 
        goods_number: 0, // 
        goods_cat: [], // 
        pics: [], // 
        goods_introduce: '', // 
        attrs: [] // 
      },
      // 
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      catelist: [], // 
      // 
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      manyTabData: [], // 
      onlyTabData: [], // 
      // URL
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // upload headers
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      previewPath: '', // URL
      previewVisible: false // 
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catelist = res.data
    },

    // 
    handleChange() {
      // 
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },

    // tab 
    beforeTabLeave(activeName, oldActiveName) {
      // activeName 
      // oldActiveName 
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false // return false 
      }
    },

    // tab 
    async tabClicked() {
      // 
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        // attr_vals
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
        })

        this.manyTabData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.onlyTabData = res.data
      }
    },

    // 
    handlePreview(file) {
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },

    // 
    handleRemove(file) {
      // 
      const filePath = file.response.data.tmp_path
      // pics , 
      const i = this.addForm.pics.findIndex(item => {
        return item.pic === filePath
      })
      // splice()pics
      this.addForm.pics.splice(i, 1)
    },

    // 
    handleSuccess(response) {
      // 
      // 1. 
      const picInfo = { pic: response.data.tmp_path }
      // 2. pushpics
      this.addForm.pics.push(picInfo)
    },

    // 
    addGoods() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')

        // 
        // const cloneForm = _.cloneDeep(this.addForm)
        // const cloneForm = JSON.parse(JSON.stringify(this.addForm)) // 
        const cloneForm = { ...this.addForm } // 
        cloneForm.goods_cat = cloneForm.goods_cat.join(',')
        // 
        this.manyTabData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(',')
          }
          this.addForm.attrs.push(newInfo)
        })
        // 
        this.onlyTabData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs.push(newInfo)
        })
        // cloneFormaddForm
        cloneForm.attrs = this.addForm.attrs

        // 
        const { data: res } = await this.$http.post('goods', cloneForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }
  },

  computed: {
    // id
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[this.addForm.goods_cat.length - 1]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .el-tabs__item {
  font-weight: normal;
}

.add-goods {
  margin-top: 30px;
}

.el-checkbox {
  margin: 0 8px 0 0 !important;
}

.addBtn {
  margin-top: 15px;
}
</style>
