<template>
  <div class="login-container">
    <div class="login-box">
      <!--  -->
      <div class="avatar-box">
        <img src="../assets/logo.png" alt />
      </div>
      <!--  -->
      <el-form :model="loginForm" :rules="loginFormRules" ref="loginFormRef" class="login-form">
        <!-- :  -->
        <el-form-item prop="username">
          <el-input v-model.trim="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!-- :  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" show-password prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <!-- :  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      // 
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 10, message: ' 5  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // , 
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },

    // , 
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        // 
        if (!valid) return
        // ajax
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // , token
        window.sessionStorage.setItem('token', res.data.token)
        // , home
        this.$router.push('/home')
      })
    }
  }
}
</script>

